<template>
  <div>
    <header>
      <h1> </h1>
    </header>
    <main>
      <div>
        <div class="user-info-container">
      <v-row justify="center">
        <v-col cols="12" md="6" class="margin-bottom">
          <v-text-field
              v-model="user.nickname"
              label=""
              variant="solo"
              readonly
              dense
              style="width: 100%; margin-right: 20px;"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" md="6" class="margin-bottom">
          <v-text-field
              v-model="user.email"
              label=""
              variant="solo"
              readonly
              dense
              style="width: 100%; margin-right: 20px;"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" md="6" class="margin-bottom">
          <v-text-field
              v-model="user.address"
              label=""
              variant="solo"
              readonly
              dense
              style="width: 100%; margin-right: 20px;"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" md="6" class="margin-bottom">
          <v-text-field
              v-model="user.contact"
              label=""
              variant="solo"
              readonly
              dense
              style="width: 100%; margin-right: 20px;"
          ></v-text-field>
        </v-col>
      </v-row>
        </div>
      </div>
    </main>
    <footer>
      <button @click="goBack" class="bordered-button"> </button>
      <button @click="updateProfileOnBackend" class="bordered-button">  </button>
      <button @click="updateRoleOnBackend" class="bordered-button"> </button>
    </footer>
  </div>
</template>

<script>
import router from "@/routers";
import axios from "axios";

export default {
  data() {
    return {
      user: {
        nickname: "",
        email: "",
        address: "",
        contact: ""
      },
    };
  },
  created() {
    this.fetchUserInfo();
  },
  methods: {
    fetchUserInfo() {
      const accessToken = localStorage.getItem('accessToken');
      if (!accessToken) {
        window.alert('  .');
        return;
      }

      axios.get('http://43.200.245.57:8080/api/v1/users', {
        headers: {
          'Authorization': `${accessToken}`,
        },
      })
          .then(response => {
            console.log(response.data);
            this.user = response.data.data;
          })
          .catch(error => {
            console.error(error);
          });
    },
    goBack() {
      router.push('/');
    },
    updateProfileOnBackend() {
      router.push('updateuserinfo');
    },
    updateRoleOnBackend() {
      const accessToken = localStorage.getItem('accessToken');

      axios.patch('http://43.200.245.57:8080/api/v1/users/role', {
      }, {
        headers: {
          'Authorization': `${accessToken}`,
          'Content-Type': 'application/json',
        },
      })
          .then(response => {
            window.alert("  ");
            console.log(response.data);
          })
          .catch(error => {
            window.alert("  ");
            console.error(error);
          });
    },
  },
};
</script>


